<template>
  <div id="add-blog">
    <h1 v-if="!submitted">发布</h1>
    <h1 v-if="submitted">成功</h1>
    <form v-if="!submitted">
      <label>博客标题</label>
      <input type="text" v-model="blog.title" required>

      <label>博客内容</label>
      <textarea v-model="blog.content"></textarea>


      <label for="">作者：</label>
      <select v-model='blog.author'>
        <option v-for='author in authors'>{{author}}</option>
      </select>
       <div id="checkboxes">
        <label for="">Vue.js</label>
        <input type="checkbox" value="Vue" v-model='blog.categories'>
        <label for="">node.js</label>
        <input type="checkbox" value="node" v-model='blog.categories'>
        <label for="">js</label>
        <input type="checkbox" value="js" v-model='blog.categories'>
        <label for="">html</label>
        <input type="checkbox" value="h5c3" v-model='blog.categories'>
      </div>
      <button v-on:click.prevent='post'>添加博客</button>
    </form>
    <div v-if='submitted'>
      <h3>您的博客发布成功</h3>
    </div>

    <div id="preview">
      <h3>博客总览</h3>
      <p>博客标题：{{blog.title}}</p>
      <p>博客内容：</p>
      <p>{{blog.content}}</p>
      <p>博客分类：</p>
      <ul>
        <li v-for='category in blog.categories'>{{category}}</li>
      </ul>
      <div style="clear: both"></div>
      <p>作者：{{blog.author}}</p>
    </div>
  </div>
</template>

<script>
export default {
  name: 'add-blog',
  data () {
    return {
      blog:{
        title:'',
        content:'',
        categories:[],
        author:''
      },
      authors:["金景","jinjing"],
      submitted:false
    }
  },
  methods:{
    post(){
      this.$http.post('https://vuedemo-2bde9.firebaseio.com/posts.json', this.blog)
      .then(function(data){
        console.log(data);
        this.submitted = true;
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#add-blog *{
  box-sizing: border-box;
}

#add-blog{
  margin:20px auto;
  max-width:600px;
  padding:20px;
}

label {
  display: block;
  margin:20px 0 10px;
}

input[type="text"],textarea,select{
  display: block;
  width: 100%;
  padding: 8px;
  box-shadow: 10px 10px 5px #888888;
}
textarea{
  height: 200px;
  resize: none
}

#checkboxes label{
  display:inline-block;
  margin-top:20px;
}

#checkboxes input{
  display: inline-block;
  margin-right: 20px;
}

button{
  display:block;
  margin:20px 0;
  background: skyblue;
  color: #fff;
  border:0;
  padding:14px;
  border-radius:4px;
  font-size: 18px;
  cursor: pointer;
}

#preview{
  padding: 10px 20px;
  border:1px dotted #ccc;
  margin:30px 0;
  box-shadow: 10px 10px 5px #888888;
  background: #fff;
}
#h3{
  margin-top:10px;
}
#preview ul li{
  list-style: none;
  float: left;
  margin-right: 20px;
  background: #ccc;
  padding: 5px;
  font-size: 16px;
  margin-top:0;
}
</style>



