<template>
  <div id="preview">
      <h3>博客总览</h3>
      <p>博客标题：{{blog.title}}</p>
      <p>博客内容：</p>
      <p>{{blog.content}}</p>
      <p style="margin-top: 50px">博客分类：</p>
      <ul>
        <li v-for='category in blog.categories' style="font-size: 16px;">{{category}}</li>
      </ul>
      <div style="clear: both"></div>
      <p>作者：{{blog.author}}</p>
      <button @click='deleteSingleBlog()'>删除</button>
      <router-link v-bind:to="'/edit/'+id">编辑</router-link>
    </div>
</template>

<script>
  export default{
    name:'single-blog',
    data(){
      return{
        id:this.$route.params.id,
        blog:{}
      }
    },
    created(){
      this.$http.get('https://vuedemo-2bde9.firebaseio.com/posts/' + this.id +'.json')
        .then(function(data) {
          return data.json();
        })
        .then(function(data) {
          this.blog = data;
          console.log(data)
        })
    },
    methods:{
      deleteSingleBlog(){
        this.$http.delete('https://vuedemo-2bde9.firebaseio.com/posts/' + this.id +'.json')
        .then(response => {
          this.$router.push({path:'/'})
        })
      }
    }
  }
</script>

<style>
  #single-blog{
    max-width: 960px;
    margin: 0 auto;
     padding: 0 20px 20px;
    background: #eee;
    border:1px dotted #aaa;
  }
  ul{
    text-align: left;
  }
  ul li{
    margin-top: 0;
    text-align: left;
  }
  #preview{

  padding: 10px 20px;
  border:1px dotted #ccc;
  margin:30px 0;
  box-shadow: 10px 10px 5px #888888;
  background: #fff;
  max-width: 800px;
   margin:0 auto;
}
#h3{
  margin-top:10px;
}
#preview ul li{
  list-style: none;
  float: left;
  margin-right: 20px;
  background: #ccc;
  padding: 5px;
}
button{
  outline: none;
  border:none;
  background: #fff;
  font-size: 16px;
  cursor: pointer;

}
#preview a{
  color: black;
  display: inline-block;
  font-size: 16px;
}
</style>



