<template>
  <nav>
    <ul>
      <li>
        <router-link to='/' exact>博客</router-link>
        <router-link to='/add' exact>写博客</router-link>
      </li>
    </ul>
  </nav>
</template>

<script>
  export default{
    name:'blog-header'
  }
</script>

<style>
  ul{
    list-style: none;
    text-align: center;
    padding: 0;
    margin:0;
  }
  li{
    display: inline-block;
    font-size: 30px;
    margin-top: 150px;
    height: 100%;
  }
  a{
    color:white;
    text-decoration: none;
    border-radius: 5px;
    display: block;
    margin-top: 50px;
    height: 50%;
  }
  nav{
    background: linear-gradient(#fff, #333, #fff);
    margin-bottom: 40px;
    font-size: 16px;
    position: fixed;
    z-index: 999;
    height: 100%;
    width: 50px;
    left:0;
    top:0;

  }
  .router-link-active{
    background: rgba(255,255,255,0.8);
    color: #444
  }
</style>



