<template>
  <div id="show-blogs">
    <h1>博客总览</h1>
    <input type="text" v-model='search' placeholder="搜索" >
    <div class='single-blog' v-for='blog in filteredBlogs'>
      <router-link v-bind:to='"/blog/" + blog.id'><h2 v-rainbow>{{blog.title | uperc}}</h2></router-link>
      <article>
        {{blog.content | snippet}}
      </article>
    </div>
  </div>
</template>

<script>

export default {
  name: 'show-blogs',
  data(){
    return{
      blogs:[],
      search:''
    }
  },
  created(){
    this.$http.get('https://vuedemo-2bde9.firebaseio.com/posts.json')
    .then(function(data) {
      return data.json()
    })
    .then(function(data) {
      var bArray = []
      for (let key in data) {
        data[key].id = key
        bArray.push(data[key])
      }
      this.blogs = bArray
    })
  },
  computed:{
    filteredBlogs(){
      return this.blogs.filter((blog) => {
        return blog.title.match(this.search)
      })
    }
  }
}
</script>

<style>
#show-blogs{
  max-width: 800px;
  margin:0 auto;
}
.single-blog{
  padding: 0 20px 20px;
  margin:20px 0;
  box-sizing: border-box;
  background: #fff;
  border:1px solid #aaa;
  box-shadow: 10px 10px 5px #888888;
}
.single-blog a{
  margin-top: 0px;
}
#show-blogs a{
  color: #444;
  text-decoration: none;
}
input[type='text']{
  padding: 8px;
  width: 100%;
  box-sizing: border-box;
  box-shadow: 10px 10px 5px #888888;
  outline: none
}
</style>



